import React, { Component } from 'react'
import axios from 'axios'

export default class CreateUsers extends Component {
  constructor(props) {
    super(props)

    this.onSubmit = this.onSubmit.bind(this)

    this.state = {
      username: '',
      email: '',
      password: '',
      newPassword: '',
      confirmNewPassword: '',
      errorMessage: '',
      message: '',
    }
    axios.get('/api/users/userInfo').then((res) => {
      res.data.code === 0
        ? this.setState({
            username: res.data.data.username,
            email: res.data.data.email,
          })
        : this.props.history.push('/user/login')
    })
  }

  onChangeUsername(e) {
    this.setState({
      username: e.target.value,
    })
  }
  onChangePassword(e) {
    this.setState({
      password: e.target.value,
    })
  }
  onChangeNewPassword(e) {
    this.setState({
      newPassword: e.target.value,
    })
  }
  onChangeNewConfirmPassword(e) {
    this.setState({
      confirmNewPassword: e.target.value,
    })
  }
  onSubmit(e) {
    e.preventDefault()
    if (this.state.newPassword !== this.state.confirmNewPassword) {
      this.setState({
        message: '„Éë„Çπ„ÉØ„Éº„Éâ„Åå‰∏ÄËá¥„Åó„Å¶„ÅÑ„Å™„ÅÑÔºÅ',
      })
    } else {
      const user = {
        username: this.state.username,
        email: this.state.email,
        password: this.state.password,
        newPassword: this.state.newPassword,
      }
      this.setState({
        message: '',
      })
      //Ë∞ÉÁî®ÂêéÁ´ØÊé•Âè£ÂàõÂª∫user
      axios
        .post('/api/users/update', user)
        .then(() => {
          alert('Â§âÊõ¥„Åó„Åæ„Åó„ÅüÔºÅ')
          this.props.history.push('/comment')
        })
        .catch((e) => {
          this.setState({
            errorMessage: '„Éë„Çπ„ÉØ„Éº„Éâ„ÅåÈñìÈÅï„ÅÑ„Åæ„Åó„ÅüÔºÅ',
          })
          alert(e)
        })
    }
  }
  render() {
    return (
      <div>
        <h3 className='mt-3 mb-5'>„É¶„Éº„Ç∂„ÉºÊÉÖÂ†±Â§âÊõ¥</h3>
        <form onSubmit={this.onSubmit}>
          <div className='form-group'>
            <label>„Éã„ÉÉ„ÇØ„Éç„Éº„É†: </label>
            <input
              type='text'
              className='form-control mt-2 mb-4'
              value={this.state.username}
              onChange={this.onChangeUsername.bind(this)}
            />
          </div>
          <div className='form-group'>
            <label>„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ: </label>
            <span>{this.state.email}</span>
          </div>
          <div className='form-group'>
            <label>„Éë„Çπ„ÉØ„Éº„Éâ: </label>
            <span style={{ color: 'red' }}>{this.state.errorMessage}</span>
            <input
              type='password'
              className='form-control mt-2 mb-4'
              value={this.state.password}
              onChange={this.onChangePassword.bind(this)}
            />
          </div>
          <div className='form-group'>
            <label>Êñ∞„Éë„Çπ„ÉØ„Éº„Éâ: </label>
            <input
              type='password'
              className='form-control mt-2 mb-4'
              value={this.state.newPassword}
              onChange={this.onChangeNewPassword.bind(this)}
            />
          </div>
          <div className='form-group'>
            <label>Êñ∞„Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç: </label>
            <span style={{ color: 'red' }}>{this.state.message}</span>
            <input
              type='password'
              className='form-control mt-2 mb-4'
              value={this.state.confirmNewPassword}
              onChange={this.onChangeNewConfirmPassword.bind(this)}
            />
          </div>
          <div className='form-group'>
            <input type='submit' value='Â§âÊõ¥' className='btn btn-info' />
          </div>
        </form>
      </div>
    )
  }
}
