import React, { Component } from 'react'
import PropTypes from 'prop-types'

class CommentInput extends Component {
  static propTypes = {
    username: PropTypes.string,
    email: PropTypes.string,
    onSubmit: PropTypes.func,
  }

  constructor(props) {
    super(props)
    this.state = {
      username: '',
      content: '',
    }
  }

  componentDidMount() {
    this.textarea.focus()
  }

  handleUsernameChange(event) {
    this.setState({
      username: event.target.value,
    })
  }

  handleContentChange(event) {
    this.setState({
      content: event.target.value,
    })
  }

  handleSubmit(e) {
    e.preventDefault()

    if (this.props.onSubmit) {
      const { username, content } = this.state
      const email = this.props.email
      this.props.onSubmit({
        username,
        email,
        content,
        createdTime: +new Date(),
      })
    }
    this.setState({ content: '' })
  }

  render() {
    return (
      <div className='comment-input'>
        <div className='comment-field'>
          <span className='comment-field-name'>„É¶„Éº„Ç∂ÂêçÔºö</span>
          <div className='comment-field-input'>
            <input
              value={this.state.username}
              onChange={this.handleUsernameChange.bind(this)}
              // ËøôÈáåÁöÑrefÁöÑ‰ΩúÁî®ÊòØ Â∞ÜËøô‰∏™inputÂëΩÂêç‰∏∫inputName
              // Âπ∂‰∏îËÆæÁΩÆ‰∏∫ÁªÑ‰ª∂ÂÆû‰æãÁöÑ‰∏Ä‰∏™Â±ûÊÄß
              // ‰πãÂêéÂ∞±ÂèØ‰ª•ÈÄöËøáthis.inputNameÊù•Ëé∑ÂèñËøô‰∏™DOMÂÖÉÁ¥†
              // ÊØîÂ¶ÇÂèØ‰ª•Âú®componentDidMountÈáåÈù¢‰ΩøÁî®Ëøô‰∏™DOMÂÖÉÁ¥†
              // Âπ∂‰∏îË∞ÉÁî®Á±ª‰ººthis.inputName.focus()‰πãÁ±ªÁöÑDOM API
              // Â∞±ÂèØ‰ª•ËææÂà∞Âä†ËΩΩÂÆåÈ°µÈù¢Ëá™Âä®focusÂà∞ËæìÂÖ•Ê°ÜÁöÑÊïàÊûú
              // *** ‰ΩÜÊòØÔºÅÔºÅÔºÅËÉΩÁî®reactÁöÑÊú∫ËÉΩÂÅöÂà∞ÁöÑ‰∫ã‰∏ÄÂÆö‰∏çË¶ÅÁî®ref
              // *** Âπ∂‰∏î ÊÄª‰πã‰∏çÂà∞‰∏á‰∏çÂæóÂ∑≤Â∞ΩÈáè‰∏çË¶ÅÁî®ref
              // ref={(inputName) => (this.inputName = inputName)}
            />
          </div>
        </div>
        <div className='comment-field'>
          <span className='comment-field-name'>„Ç≥„É°„É≥„ÉàÔºö</span>
          <div className='comment-field-input'>
            <textarea
              ref={(textarea) => (this.textarea = textarea)}
              value={this.state.content}
              onChange={this.handleContentChange.bind(this)}
            />
          </div>
        </div>
        <div className='comment-field-button'>
          <button onClick={this.handleSubmit.bind(this)}>ÊäïÁ®ø</button>
        </div>
      </div>
    )
  }
}

export default CommentInput
